
.second_list {
  width: 180px;
  position: relative;
}
.second_list li {
  list-style: none;
}
.second_list label {
  width: auto!important;
}
.second_list .checkAll .el-checkbox__input.is-checked .el-checkbox__inner {
  background-color: rgba(85, 217, 233, 0.1);
  border-color: rgba(85, 217, 233, 0.2);
}
.second_list .checkAll .el-checkbox__input.is-checked .el-checkbox__inner:after {
  border-color: #55d9e9;
}
.second_list .el-input .el-input__icon {
  color: #55d9e9!important;
  border-left: none;
}
.second_list .el-input .el-input__icon:before {
  content: "\E603";
}
.second_list .el-input.is-disabled .el-input__inner {
  background-color: #fff;
  border-color: #55d9e9;
  color: #333;
  cursor: default;
}
.second_list .index_input i {
  text-align: left;
  padding: 0 10px;
  width: 100%;
}
.second_list .el-input__icon {
  border-left: none;
}
.second_list > ul {
  padding: 10px 0;
  position: absolute;
  width: 180px;
  background-color: #fff;
  z-index: 2;
  white-space: nowrap;
  margin: 5px 0;
  border: 1px solid #d1dbe5;
  height: 300px;
}
.second_list > ul > li {
  padding: 8px 15px;
  cursor: pointer;
}
.second_list > ul > li:hover,
.second_list > ul > li:active {
  background-color: #f2f2f2;
}
.second_list > ul > li > ul {
  padding: 10px 0;
  border: 1px solid #d1dbe5;
  border-left: none;
  min-width: 200px;
  position: absolute;
  left: 181px;
  top: -1px;
  height: 300px;
  overflow: auto;
  background-color: #fff;
}
.second_list > ul > li > ul > li {
  color: #454545;
  padding: 8px 15px;
}
.second_list > ul > li:after {
  font-family: element-icons;
  content: "\E606";
  font-size: 12px;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
  color: #bfcbd9;
  position: absolute;
  right: 10px;
  margin-top: 1px;
}
.second_list > ul li {
  font-size: 14px;
}
.second_list > ul li label {
  width: auto!important;
  margin-right: 10px;
}
.second_list > ul li .checkAll {
  padding: 0;
}
.second_list > ul li .checkAll .el-checkbox__input {
  line-height: 0;
  vertical-align: bottom;
}



